// ─── Full height dans le slideover (full screen mobile) ──
:host {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: 0;
}

// ─── Racine : permet au body de scroller, footer reste en bas ──
.paiement-panel {
    min-height: 0;
    overflow: hidden;
}

// ─── Mobile only : fenêtre plein écran alignée, bouton toujours visible ──
@media (max-width: 768px) {
    :host {
        width: 100%;
        max-width: 100%;
        overflow: hidden;
    }

    .paiement-panel {
        width: 100%;
        max-width: 100%;
    }

    .paiement-footer {
        padding-bottom: calc(1.5rem + env(safe-area-inset-bottom));
    }
} 

// ─── Payment mode cards ──────────────────────────────────
.slideover-mode {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    padding: 1rem 0.75rem;
    border-radius: 12px;
    border: 1.5px solid var(--p-surface-200);
    background: transparent;
    cursor: pointer;
    transition: all 0.15s ease;
    color: var(--p-surface-500);
    font-size: 0.82rem;
    font-weight: 500;

    i {
        font-size: 1.25rem;
    }

    &:hover {
        border-color: var(--p-surface-300);
        background: var(--p-surface-50);
    }

    &--active {
        border-color: var(--p-primary-500);
        background: var(--p-primary-50);
        color: var(--p-primary-600);
        font-weight: 600;
    }
}

:host-context(.app-dark) {
    .slideover-mode {
        border-color: var(--p-surface-600);
        color: var(--p-surface-300);

        &:hover {
            background: var(--p-surface-800);
            border-color: var(--p-surface-500);
        }

        &--active {
            border-color: var(--p-primary-500);
            background: rgba(59, 130, 246, 0.12);
            color: var(--p-primary-400);
        }
    }
}
